	module.exports = {"data":{"post":{"html":"<p>I asked this question on Twitter and got a lot of guesses, so I'll try it here for the blog crowd. </p>\n<p>In February we're about to start another coffee cup giveaway with our McMenüs (Value Meals). Even though the cup is free, we have to adjust our Value Added Tax (similar to sales tax) programming in the registers. Why do you think we have to pay VAT on an item that is free?</p>\n<p>So you don't get distracted... yes, Germany has different VAT rates for restaurant food eaten in or taken out. A porcelain cup is not food, so is taxed the same regardless. The answer has nothing to do with eat-in vs take-out.</p>\n<p><strong>Update:</strong> The answer is <a href=\"http://www.papascott.de/archives/2010/01/26/value-added-tax-on-nothing/#comment-40677\">below</a> in the comments, for everyone curious about German VAT regulations!</p>","fields":{"title":"Value Added Tax on Nothing","date":"26 Jan 2010","url":"https://papascott.de/2010-01-26-value-added-tax-on-nothing/"}}},"pathContext":{"slug":"/2010-01-26-value-added-tax-on-nothing/","prev":{"fields":{"title":"New Sensation","date":"11 Jan 2010","slug":"/2010-01-11-new-sensation/"}},"next":{"fields":{"title":"Male Striptease...","date":"03 Feb 2010","slug":"/2010-02-03-male-striptease/"}}}}

